body {
  --hover-color: #00acdc;
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  position: relative;
  left: 0;
  right: 0;
}

.logo-link {
  height: 40px;
}

.logo-image {
  height: 100%;
}

.nav {
  display: flex;
}

.nav__link {
  text-transform: uppercase;
  color: #000;
  height: 60px;
  margin-right: 20px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
}

.nav__link:last-child {
  margin-right: 0;
}

.nav__link:link, .nav__link:visited {
  color: #000;
}

.nav__link:hover {
  color: var(--hover-color);
}

.nav__link.is-active {
  color: var(--hover-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--hover-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}
